import React from "react";
import { ThemeToggler } from "gatsby-plugin-dark-mode";
import './ThemeToggle.module.scss'

import {  store,view } from 'react-easy-state'
import { storage } from 'react-easy-params'

const themeChanger = store({
  isDark: false,
  change: () => {
    themeChanger.isDark = !themeChanger.isDark;
    storage.isDark = themeChanger.isDark;
  }
});

export default view(function ThemeToggle() {
  return (
    <ThemeToggler>
      {({ theme, toggleTheme }) => {
        if (theme == null) {
          return null;
        }
        return (
          <div className="theme-switch-wrapper">
            <label className="theme-switch">
              <input
                type="checkbox"
                onChange={e => {toggleTheme(e.target.checked ? "light" : "dark"); themeChanger.change()}}
                checked={theme === "light"}
              />
            </label>
          </div>
        );
      }}
    </ThemeToggler>
  );
})
